.SidePanel {
  float: right;
  position: absolute;
  right: 0;
  top: 80px;
  height: 89.8vh;
  width: 16vw;
  border-left: 1px solid rgba(151, 151, 151);
  z-index: 2;
}

.message-list {
    box-sizing: border-box;
    padding: 0 0 0 6px;
    width: 100%;
    height: 89%;
    overflow: scroll;
    background-color: white;
    --main-color: #5ea3d0;
    --secondary-color: white;
    --main-text-color: #3e5869;
    --secondary-text-color: #b0c7d6;
    --send-message-form: #F5F5F5;
    overflow-x: hidden;
}

ul {
    list-style-type:none;
}

.message div:nth-child(1) {
    font-size: 11px;
    color: var(--main-text-color);
    opacity: 0.9;
    margin-bottom: 6px;
}
.message div:nth-child(2) {
    background: var(--main-color);
    color: var(--secondary-color);
    display: inline;
    padding: 4px 8px;
    border-radius: 8px;
}

.leftAlign {
    float: left;
    margin: 10px 0;
    display: block;
    clear: both;
    margin-left: 10px;
    text-align: left;
}
.rightAlign {
    text-align: right;
    margin-right: 10px !important;
    clear: both;
    float: right;
    margin: 10px 0;
    display: block;
}
.send-message-form {
    background: var(--send-message-form);
    display: flex;
    box-sizing: border-box;
    height: 60px;
    background-color: rgb(241, 241, 241);
    margin-top: -20px;
}


.send-message-form input {
    width: 100%;
    padding: 15px 10px;
    border: none;
    margin: 0;
    background: var(--send-message-form);
    font-weight: 200;
}

.send-message-form input:focus {
    outline-width: 0;
}

.send-message-form input::-webkit-input-placeholder {
    color: black;
}

.send-message-form input::-ms-input-placeholder {
    color: black;
}

.send-message-form input::placeholder {
    color: black;
}

